<template>
    <div class="report">
        <header-index :index="5" v-on:getLoginRequest="getLoginRequest"></header-index>
        <vue-swiper v-if="imgs" :Imgs="imgs" :Interval="30"></vue-swiper>
        <div class="main">
            <div class="main-top">
                <div class="top">
                    <span></span>
                    <img src="../assets/xia.png" class="xia">
                </div>
            </div>
            <div class="items">
                <ul class="items-ul">
                    <li class="items-li" v-if="reports" v-for="(item, index) in reports" :key="index">
                        <div class="items-div">
                            <div class="items-top">
                                <div class="items-topleft">
                                    <img v-if="item.headimg" :src="item.headimg" class="headimg">
                                    <p class="shangjia"><span class="realshangjia">{{item.people}}</span></p>
                                    <p class="time">{{item.time}}<span class="realtime">09:35</span></p>
                                    <p class="laizi"> </p>
                                </div>
                                <div class="items-topright">
                                    <span class="liulan">{{item.times}}</span>
                                </div>
                            </div>
                            <div class="items-content">
                                {{item.content}}
                            </div>
                            <div class="items-imgs">
                                <img v-for="(imgItem, index) in item.imgs" :src="imgItem" :key="index" class="img">
                            </div>
                            <span class="shenhe"></span>
                            <button class="look"></button>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="cover" :class="{none: isCover}">

        </div>
        <div class="loginAlert" :class="{none: isCover}">
            <div class="login-title">
                <img src="../assets/reallogo.png"><img src="../assets/kaifa.png">
            </div>
            <div class="third">
                <div class="fenge">

                </div>
                <div class="fenge-title">
                    
                </div>
                <div class="fenge">

                </div>
            </div>
            <div class="third-content">
                <a href="#"><img src="../assets/qq.png" class="toguanwang"></a>
                <a href="#"><img src="../assets/wx.png" class="toguanwang"></a>
                <a href="#"><img src="../assets/wb.png" class="toguanwang"></a>
            </div>
            <div class="denglu">
                <div class="fenge">

                </div>
                <div class="denglu-title">
                    
                </div>
                <div class="fenge">

                </div>
            </div>
            <div class="denglu-content">
                <input type="text" id="nickname" placeholder="" v-model="username">
                <input type="password" id="password" placeholder="" v-model="password">
                <button class="submit" @click="login()"></button>
            </div>
            <div class="denglu-bottom">
                <a href="#"><img src="../assets/right.png"></a>
                <router-link to="/register"><img src="../assets/right2.png"></router-link>
            </div>
        </div>
        <div class="user" :class="{none: !logining}">
            <div class="user-main">
                <span class="user_name">{{user_name}}</span>
                <span class="logout" @click="logout()"></span>
            </div>
        </div>
        <footer-index></footer-index>
    </div>
</template>

<script>
import qs from 'qs';
export default {
    name: 'Report',
    data () {
        return {
            username: "",
            password: "",
            imgs: [require('../assets/lunbo2.png'), require('../assets/lunbo4.png')],
            reports: [
                {
                    headimg: require('../assets/headimg1.png'),
                    people: "",
                    content: "20181113",
                    time: "2018113",
                    times: 5443,
                    imgs: [require("../assets/jingdian.png"), require("../assets/jingdian2.png"), require("../assets/jingdian3.png")]
                },
                {
                    headimg: require('../assets/headimg2.png'),
                    people: "",
                    content: "2018321",
                    time: "2018321",
                    times: 1000,
                    imgs: [require("../assets/dongbei.png"), require("../assets/dongbei2.png"), require("../assets/dongbei3.png")]
                },
                {
                    headimg: require('../assets/headimg6.png'),
                    people: "",
                    content: "2018530",
                    time: "2018530",
                    times: 3423,
                    imgs: [require("../assets/yucun.png"), require("../assets/yucun2.png"), require("../assets/yucun3.png")]
                },
                {
                    headimg: require('../assets/headimg4.png'),
                    people: "",
                    content: "2018421",
                    time: "2018421",
                    times: 6423,
                    imgs: [require("../assets/yingtao.png"), require("../assets/yingtao2.png"), require("../assets/yingtao3.png")]
                },
                {
                    headimg: require('../assets/headimg6.png'),
                    people: "",
                    content: "201710",
                    time: "20171020",
                    times: 6423,
                    imgs: [require("../assets/youzi.png"), require("../assets/youzi2.png"), require("../assets/youzi3.png")]
                },
                {
                    headimg: require('../assets/headimg7.png'),
                    people: "",
                    content: "201851210",
                    time: "2018512",
                    times: 4423,
                    imgs: [require("../assets/youshan.png"), require("../assets/youshan2.png"), require("../assets/youshan3.png")]
                },
                {
                    headimg: require('../assets/headimg10.png'),
                    people: "",
                    content: "2018412",
                    time: "2018412",
                    times: 1923,
                    imgs: [require("../assets/boluo.png"), require("../assets/boluo2.png"), require("../assets/boluo3.png")]
                },
                {
                    headimg: require('../assets/headimg8.png'),
                    people: "",
                    content: "2018723",
                    time: "2018723",
                    times: 7923,
                    imgs: [require("../assets/nongjiale.png"), require("../assets/nongjiale2.png"), require("../assets/nongjiale3.png")]
                },
                {
                    headimg: require('../assets/headimg9.png'),
                    people: "",
                    content: "20186",
                    time: "2018620",
                    times: 8923,
                    imgs: [require("../assets/shanshui.png"), require("../assets/shanshui2.png"), require("../assets/shanshui3.png")]
                },
                {
                    headimg: require('../assets/headimg3.png'),
                    people: "",
                    content: "201872",
                    time: "201872",
                    times: 12923,
                    imgs: [require("../assets/gou6.jpg"), require("../assets/paomo.jpg"), require("../assets/paomo2.jpg")]
                }
            ],
            url: "http://gm.credog.top/app",
            user_name: "",
            logining: false,
            isCover: true
        }
    },
    created() {
        if (window.localStorage.getItem('username')) {
            this.user_name = window.localStorage.getItem('username');
            this.logining = true;
        }
        this.$http.post(this.url + "/forPoor/session")
            .then((response) => {
                console.log(response);
                if (response.data.status == "false") {
                    window.localStorage.setItem('src', "");
                    window.localStorage.setItem('username', "");
                    this.logining = false;
                } else if (response.data.status == "ok") {
                    this.logining = true;
                }
            })
    },
    mounted() {

    },
    beforeDestroy() {
    },
    methods: {
        logout: function () {
            this.$http.post(this.url + "/forPoor/logout")
                .then((response) => {
                    console.log(response);
                    if (response.data.status == "ok") {
                        window.localStorage.setItem('src', "");
                        window.localStorage.setItem('username', "");
                        this.logining = false;
                    }
                });
        },
        getLoginRequest: function () {
            this.isCover = false;
        },
        login: function () {
            let params = {
                username: this.username,
                password: this.password,
            };
            this.$http.post(this.url + "/forPoor/login", qs.stringify(params), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
                .then((response) => {
                    console.log(response);
                    if (response.data.status == "ok") {
                        this.isCover = true;
                        this.logining = true;
                        this.user_name = response.data.username;
                        window.localStorage.setItem('src', response.data.headImg);
                        window.localStorage.setItem('username', response.data.username);
                        // this.$store.commit('writeSrc', {data: response.data.headImg});
                        // this.$store.commit('writeUsername', {data: response.data.username});
                    } else if (response.data.status == "false") {
                        alert("");
                    } else if (response.data.status == "no") {
                        alert("");
                    } else if (response.data.status == "logined") {
                        alert("");
                    }
                });
        }
    }
}
</script>

<style scoped>
.report {
    position: relative;
    width: 100%;
}

.main {
    position: relative;
    width: 100%;
    background: #f7f7f7;
    padding-top: 40px;
}

.main-top {
    height: 55px;
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ffffff;
}

.top {
    position: relative;
    display: flex;
    align-items: center;
    max-width: 1010px;
    min-width: 1010px;
}

.top span {
    display: inline-block;
    line-height: 55px;
    font-size: 26px;
    color: #333333;
    margin-left: 10px;
}

.xia {
    position: absolute;
    right: 0;
}

.items {
    position: relative;
    width: 100%;
}

.items-ul {
    position: relative;
    display: inline-block;
    width: 100%;
}

.items-li {
    position: relative;
    width: 100%;
    margin-top: 30px;
    border-bottom: 10px solid #ffffff;
}

.items-div {
    position: relative;
    max-width: 800px;
    min-width: 800px;
    width: 920px;
    padding: 20px 100px 20px 100px;
    border: 1px solid #eeeeee;
    background: #ffffff;
    box-shadow: 0px 0px 10px #cccccc;
    color: #666666;
    font-size: 14px;
    margin: 0 auto;
    margin-bottom: 30px;
}

.items-top {
    position: relative;
    display: flex;
    align-items: flex-start;
    height: 60px;
    margin-bottom: 10px;
}

.items-topleft {
    display: inline-block;
    width: 700px;
}

.headimg {
    display: inline-block;
    width: 55px;
    height: 55px;
    border-radius: 55px;
    float: left;
    border: 1px solid #aaaaaa;
}

.shangjia {
    position: absolute;
    display: inline-block;
    margin-top: 10px;
    margin-left: 10px;
}

.time {
    position: absolute;
    display: inline-block;
    font-size: 12px;
    margin-top: 37px;
    margin-left: 10px;
}

.realtime {
    display: inline-block;
    margin-left: 10px;
}

.realshangjia {
    color: #e40c07;
}

.laizi {
    position: absolute;
    display: inline-block;
    font-size: 12px;
    margin-top: 37px;
    margin-left: 210px;

}

.items-topright {
    position: relative;
    display: inline-block;
    width: 100px;
    height: 60px;
}

.items-content {
    line-height: 26px;
    font-size: 24px;
    margin-bottom: 20px;
    line-height: 30px;
}

.items-imgs img {
    display: inline-block;
    width: 199px;
    height: 149px;
    margin-right: 10px;
}

.liulan {
    position: absolute;
    right: 0;
    top: 10px;
    display: inline-block;
    font-size: 12px;
}

.shenhe {
    font-size: 12px;
    color: #999999;
}

.look {
    position: relative;
    display: inline-block;
    cursor: pointer;
    width: 120px;
    height: 52px;
    background: #f7f7f7;
    color: #666666;
    font-size: 18px;
    border: 1px solid #eeeeee;
    left: 580px;
    top: -5px;
}

.look:hover {
    color: #ffffff;
    background: #fbd437;
}

.cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 98;
    opacity: 0.4;
    background: #cccccc;
}

.loginAlert {
    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: -280px;
    margin-left: -365px;
    z-index: 99;
    background: #ffffff;
    width: 730px;
    height: 560px;
}

.login-title {
    position: relative;
    display: flex;
    height: 95px;
    justify-content: center;
    align-items: center;
}

.login-title img:first-child {
    width: 55px;
    height: 55px;
    margin-right: 10px;
}

.login-title img:last-child {
    width: 205px;
    height: 32px;
}

.third {
    position: relative;
    height: 30px;
    width: 430px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fenge {
    width: 125px;
    height: 3px;
    background: #e9e9e9;
}

.fenge-title {
    width: 210px;
    color: #999999;
    line-height: 30px;
    text-align: center;
}

.third-content {
    position: relative;
    margin: 0 auto;
    width: 240px;
    margin-top: 10px;
}

.toguanwang {
    width: 60px;
    height: 60px;
    margin-left: 8px;
    margin-right: 8px;
}

.denglu {
    position: relative;
    height: 30px;
    width: 430px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
}

.denglu-title {
    width: 210px;
    color: #999999;
    line-height: 30px;
    text-align: center;
}

.denglu-content {
    width: 325px;
    position: relative;
    margin: 0 auto;
    margin-top: 20px;
}

#nickname,#password {
    width: 298px;
    height: 38px;
    line-height: 38px;
    border: 1px solid #e7e7e7;
    padding: 0 10px;
    color: #666666;
    margin-bottom: 23px;
}

.submit {
    width: 318px;
    height: 38px;
    line-height: 38px;
    color: #ffffff;
    font-size: 18px;
    border: 1px solid #e7e7e7;
    background: #fbd437;
    cursor: pointer;
}

.denglu-bottom {
    position: relative;
    margin: 0 auto;
    margin-top: 30px;
    width: 325px;
    font-size: 14px;
    color: #999999;
}

.denglu-bottom a {
    display: inline-block;
    color: #999999;
}

.denglu-bottom a:first-child {
    margin-right: 100px;
}

.denglu-bottom a:first-child img {
    display: inline-block;
    position: relative;
    left: 1px;
}

.denglu-bottom a:last-child {
    color: #0024a8;
}

.denglu-bottom a:last-child img {
    display: inline-block;
    position: relative;
    top: 1px;
}

.user {
    position: absolute;
    width: 100%;
    right: 10px;
    top: 0;
}

.user-main {
    position: relative;
    height: 30px;
    max-width: 1010px;
    min-width: 1010px;
    text-align: right;
    margin: 0 auto;
    z-index: 20;
}

.user_name {
    position: relative;
    display: inline-block;
    top: 0;
    left: 8px;
    height: 29px;
    line-height: 29px;
    font-size: 12px;
    color: #666666;
    width: 180px;
    text-align: right;
    margin-right: 5px;
    background: #ffffff;
}

.logout {
    position: relative;
    display: inline-block;
    height: 29px;
    left: 6px;
    line-height: 30px;
    font-size: 12px;
    width: 30px;
    color: #666666;
    background: #ffffff;
    cursor: pointer;
}

.logout:hover {
    color: #fbd437;
}

.none {
    display: none;
}
</style>



